.MatchPage {
  display: grid;
  grid-template-columns: 1fr 6fr;
  grid-column-gap: 20px;

  .year-selector {
    justify-self: end;
    padding: 20px;
  }

  .page-heading {
    margin-bottom: 20px;
  }

  .team-did-not-played {
    outline: 1px solid white;
    height: 200px;
    width: 500px;
    padding: 70px;
    justify-self: center;
    align-self: center;
    border-radius: 20px;
    text-align: center;
  }
}
