.HomePage {
  .header-section {
    align-self: center;
    justify-self: center;
    text-align: center;
  }

  .app-name {
    font-size: 4rem;
    margin: 30px;
    background: -webkit-linear-gradient(#eee, rgb(102, 119, 62));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .team-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 10px;
    row-gap: 10px;
  }
}
